.header {
  width: 100%;
  height: 770px;
  background: url("../app/img/bg/bg.png") 0 0/cover no-repeat;
  padding: 30px 0;
  margin-bottom: 100px;
  background-position: center;
}
.header .hero {
  position: relative;
  width: 100%;
  height: 100%;
  color: #ffffff;
}
.header .hero .hero_bg {
  position: absolute;
  width: 100%;
  object-fit: none;
}
.header .hero .hero_container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header .hero .hero_container .hero_main-text h1 {
  font-family: "Bebas Neue", cursive;
  font-size: 60px;
  font-weight: 400;
  line-height: 70px;
  letter-spacing: 5.70937px;
  text-align: center;
}
.header .hero .hero_container .hero_main-text p {
  font-size: 16px;
  line-height: 19px;
  text-align: center;
}
.header .hero .hero_container .hero_scroll-down {
  font-size: 11px;
  line-height: 13px;
  text-align: center;
  letter-spacing: 3.7px;
  cursor: pointer;
}
.header .hero .hero_container .hero_scroll-down span:hover {
  color: #00A4E5;
  transition: 0.3s ease;
}
.header .header_nav {
  position: sticky;
  top: 0;
  margin-top: 30px;
  height: 65px;
  width: 100%;
  border-bottom: 1px solid #E6E6E6;
}
.header .header_nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header .header_nav .container .logo {
  width: fit-content;
}
.header .header_nav .container .logo object {
  pointer-events: none;
  width: 100px;
  height: 20px;
}
.header .header_nav .container .nav_items ul {
  display: flex;
  gap: 3em;
}
.header .header_nav .container .nav_items ul li {
  width: 50px;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  letter-spacing: 1.09091px;
  text-align: center;
  cursor: pointer;
}
.header .header_nav .container .nav_items ul li:hover {
  color: #00A4E5;
  transition: 0.3s ease;
}
.header .header_nav .container .nav_items__active ul {
  top: 0 !important;
}
.header .header_nav .container .nav_burger {
  z-index: 2;
  cursor: pointer;
}
.header .header_nav .container .nav_burger img {
  pointer-events: none;
}

.about-us {
  margin-bottom: 100px;
}
.about-us .container .about-us_title {
  margin-bottom: 50px;
}
.about-us .container .about-us_title h3 {
  position: relative;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 2.30625px;
}
.about-us .container .about-us_title h3:after {
  position: absolute;
  content: "";
  display: block;
  width: 50px;
  left: calc(50% - 25px);
  bottom: -15px;
  border-bottom: 3px solid #00A4E5;
}
.about-us .container .about-us_decription {
  display: flex;
  justify-content: space-between;
}
.about-us .container .about-us_decription .description__text {
  width: 400px;
  margin-right: 10px;
}
.about-us .container .about-us_decription .description__text .description__text-title {
  margin-bottom: 30px;
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
  align-items: flex-end;
  letter-spacing: 0.1875px;
}
.about-us .container .about-us_decription .description__text .description__text-body {
  font-size: 13px;
  line-height: 23px;
  align-items: center;
  letter-spacing: 0.1875px;
}
.about-us .container .about-us_decription .description__images {
  display: flex;
  gap: 20px;
  width: 50%;
}
.about-us .container .about-us_decription .description__images .description__images-container {
  position: relative;
  width: 33.3%;
  height: 224px;
}
.about-us .container .about-us_decription .description__images .description__images-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-us .container .about-us_decription .description__images .description__images-container .description__images-text {
  position: absolute;
  top: 20px;
  left: 10px;
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  color: #ffffff;
  align-items: center;
  letter-spacing: 0.1875px;
}

.benefits {
  margin-bottom: 100px;
}
.benefits .container .benefits-title_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 70px;
}
.benefits .container .benefits-title_container .benefits_title {
  position: relative;
  width: 200px;
}
.benefits .container .benefits-title_container .benefits_title h3:after {
  position: absolute;
  content: "";
  display: block;
  width: 50px;
  left: 0;
  bottom: -15px;
  border-bottom: 3px solid #00A4E5;
}
.benefits .container .benefits-title_container .benefits_service-btn {
  display: flex;
  justify-content: space-between;
  width: 60%;
}
.benefits .container .benefits-title_container .benefits_service-btn .service-btn_text {
  width: 70%;
  font-size: 11px;
  line-height: 19px;
  letter-spacing: 0.2px;
}
.benefits .container .benefits_list {
  display: flex;
  gap: 30px;
  height: 300px;
}
.benefits .container .benefits_list .benefits_list-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  border: 1px solid #E6E6E6;
}
.benefits .container .benefits_list .benefits_list-item .item-title {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.426667px;
}
.benefits .container .benefits_list .benefits_list-item .item-text {
  font-size: 13px;
  line-height: 23px;
  letter-spacing: 0.1875px;
}

.get-info {
  height: 500px;
  background: url("../app/img/bg/get-ingo-bg.png") 0 0/cover no-repeat;
  background-position: center;
}
.get-info .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.get-info .container .get-info_title {
  width: 400px;
  margin-bottom: 30px;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  letter-spacing: 0.15px;
}

.footer {
  color: #ffffff;
}
.footer .footer_signup {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  background-color: #171819;
}
.footer .footer_signup .footer_signup-title {
  margin-right: 70px;
  min-width: 205px;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.846154px;
}
.footer .footer_signup .footer_signup-form form {
  display: flex;
}
.footer .footer_signup .footer_signup-form form input {
  width: 250px;
  padding: 10px 16px;
  font-size: 14px;
  line-height: 16px;
  border: 1px solid #979797;
}
.footer .footer_signup .footer_signup-form form .form-btn {
  margin: 0;
}
.footer .footer_contact-info {
  background-color: #141A20;
}
.footer .footer_contact-info .container {
  display: flex;
  align-items: center;
  height: 240px;
  gap: 30px;
}
.footer .footer_contact-info .container .contact-info_logo {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  width: 37%;
  height: 150px;
}
.footer .footer_contact-info .container .contact-info_logo .logo_company {
  font-size: 11px;
  line-height: 13px;
  text-align: center;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_logo .logo_link {
  margin-left: -5px;
}
.footer .footer_contact-info .container .contact-info_logo .logo_text {
  font-size: 11px;
  line-height: 19px;
  letter-spacing: 0.85px;
}
.footer .footer_contact-info .container .contact-info_links {
  width: 40%;
}
.footer .footer_contact-info .container .contact-info_links p {
  margin-bottom: 15px;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_links .links-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 125px;
}
.footer .footer_contact-info .container .contact-info_links .links-list .links-list_item {
  width: fit-content;
  font-size: 11px;
  line-height: 25px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_contacts {
  width: 23%;
}
.footer .footer_contact-info .container .contact-info_contacts p {
  margin-bottom: 15px;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_contacts .contacts_address p,
.footer .footer_contact-info .container .contact-info_contacts .contacts_address a {
  font-size: 11px;
  line-height: 16px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_contacts .contacts_address a {
  display: block;
  margin-bottom: 15px;
}
.footer .footer_contact-info .container .contact-info_social {
  display: flex;
  align-self: flex-start;
  gap: 25px;
  padding-top: 60px;
}
.footer .footer_contact-info .container .contact-info_social .social {
  width: 12px;
  height: 12px;
  filter: invert(1);
}
.footer .footer_agreement {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.846154px;
  background-color: #171819;
}

.blue_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  width: 150px;
  height: 40px;
  background-color: #00A4E5;
  transition: 0.3s ease;
  cursor: pointer;
}
.blue_btn a {
  color: #ffffff;
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.5px;
}
.blue_btn:hover {
  opacity: 0.8;
}

@media (max-width: 768px) {
  .header {
    margin-top: 64px;
    margin-bottom: 30px;
    height: 500px;
  }
  .header .hero .hero_container .hero_main-text h1 {
    font-size: 40px;
    line-height: 50px;
  }
  .header .hero .hero_container .hero_main-text p {
    font-size: 12px;
  }
  .header .hero .hero_container .hero_scroll-down span {
    font-size: 9px;
  }
  .header .header_nav {
    position: fixed !important;
    margin: 0;
    background-color: #ffffff;
    z-index: 1;
  }
  .header .header_nav .container {
    position: relative;
  }
  .header .header_nav .container .nav_items ul {
    position: absolute;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    width: 100%;
    top: -1000px;
    left: 0;
    background: rgba(119, 119, 126, 0.25);
    backdrop-filter: blur(30px);
    transition: 0.3s ease;
  }
  .header .header_nav .container .nav_items ul li {
    width: auto;
    color: #141a20;
    font-size: 15px;
  }

  .about-us {
    margin-bottom: 40px;
  }
  .about-us .container .about-us_decription {
    flex-direction: column;
  }
  .about-us .container .about-us_decription .description__text {
    width: 100%;
    margin: 0;
    margin-bottom: 30px;
    text-align: center;
  }
  .about-us .container .about-us_decription .description__images {
    display: none;
  }

  .benefits {
    margin-bottom: 40px;
  }
  .benefits .container .benefits-title_container {
    flex-direction: column;
  }
  .benefits .container .benefits-title_container .benefits_title {
    margin-bottom: 30px;
  }
  .benefits .container .benefits-title_container .benefits_title h3 {
    text-align: center;
  }
  .benefits .container .benefits-title_container .benefits_title h3:after {
    left: calc(50% - 25px);
  }
  .benefits .container .benefits-title_container .benefits_service-btn {
    flex-direction: column;
    width: 100%;
    align-items: center;
  }
  .benefits .container .benefits-title_container .benefits_service-btn .service-btn_text {
    text-align: center;
    margin-bottom: 20px;
  }
  .benefits .container .benefits_list {
    flex-direction: column;
    height: auto;
  }
  .benefits .container .benefits_list .benefits_list-item {
    align-items: center;
    gap: 20px;
  }
  .benefits .container .benefits_list .benefits_list-item .item-text {
    text-align: center;
  }

  .footer .footer_signup {
    flex-direction: column;
    gap: 15px;
  }
  .footer .footer_signup .footer_signup-title {
    margin: 0;
  }
  .footer .footer_contact-info .container {
    position: relative;
    flex-wrap: wrap;
    height: auto;
  }
  .footer .footer_contact-info .container .contact-info_logo {
    height: auto;
    gap: 15px;
  }
  .footer .footer_contact-info .container .contact-info_links ul li {
    margin-right: 10px;
  }
  .footer .footer_contact-info .container .contact-info_social {
    position: absolute;
    bottom: 5%;
    right: 5%;
  }
}
@media (max-width: 450px) {
  .footer .footer_signup {
    padding-bottom: 10px;
  }
  .footer .footer_signup .footer_signup-form form input {
    width: 170px;
  }
  .footer .footer_contact-info .container {
    padding-top: 20px;
    position: relative;
    align-items: baseline;
    flex-wrap: wrap;
    height: auto;
  }
  .footer .footer_contact-info .container .contact-info_contacts {
    width: fit-content;
  }
  .footer .footer_contact-info .container .contact-info_links {
    width: fit-content;
    margin-left: 70px;
  }
  .footer .footer_contact-info .container .contact-info_links ul {
    flex-wrap: nowrap !important;
  }
  .footer .footer_contact-info .container .contact-info_social {
    right: 13%;
  }
}
@media (min-width: 768px) {
  .nav_burger {
    display: none;
  }
}
body {
  font-family: "Open Sans", sans-serif;
  color: #48555B;
}

.wrapper {
  overflow: hidden;
}

.container {
  max-width: 940px;
  margin: 0 auto;
  padding: 0 20px;
}

.blue_text {
  color: #00A4E5;
}

.logo {
  width: fit-content;
}
.logo object {
  pointer-events: none;
  width: 100px;
  height: 20px;
}